import React, { useEffect, useState } from "react";
import { useContext } from "react";

import { WalletSelector, Wallet } from "@near-wallet-selector/core";
import { setupWalletSelector } from "@near-wallet-selector/core";
import { setupModal, WalletSelectorModal } from "@near-wallet-selector/modal-ui";
import { setupNearWallet } from "@near-wallet-selector/near-wallet";
import { setupMyNearWallet } from "@near-wallet-selector/my-near-wallet";
import { setupSender } from "@near-wallet-selector/sender";
import { setupMathWallet } from "@near-wallet-selector/math-wallet";
import { setupNightly } from "@near-wallet-selector/nightly";
import { setupMeteorWallet } from "@near-wallet-selector/meteor-wallet";
import { setupLedger } from "@near-wallet-selector/ledger";
import { setupDefaultWallets } from "@near-wallet-selector/default-wallets";
import "@near-wallet-selector/modal-ui/styles.css";

type AppServices = {
  selector: WalletSelector;
  selectorModal: WalletSelectorModal;
  wallet: Wallet | null;
};

const AppContext = React.createContext<AppServices | null>(null);

export function NearContextProvider({ children }: { children: React.ReactNode }) {
  const [context, setContext] = useState<AppServices | null>(null);

  useEffect(() => {
    const init = async () => {
      const selector = await setupWalletSelector({
        network: "mainnet",
        modules: [
          ...(await setupDefaultWallets()),
          setupNearWallet(),
          setupMyNearWallet(),
          setupSender(),
          setupMathWallet(),
          setupNightly(),
          setupMeteorWallet(),
          setupLedger(),
        ],
      });

      const selectorModal = setupModal(selector, {
        contractId: "gopaywith.near",
      });

      selector.store.observable.subscribe(async () => {
        const wallet = await selector.wallet().catch(() => null);
        setContext({ selector, selectorModal, wallet });
      });

      const wallet = await selector.wallet().catch(() => null);
      setContext({ selector, selectorModal, wallet });
    };

    init();
  }, []);

  return <AppContext.Provider value={context}>{children}</AppContext.Provider>;
}

export function useNearContext() {
  const context = useContext(AppContext);
  return context;
}
